.Footer{
    width: 100%;
    height: 400px;
    background-image: url("https://jaseem1999.github.io/mathrukam_committee/kclogo2.png");
    background-size: cover;
    background-position: center;
  

}
.innerFooter{
   width: 100%;
   height: 400px;
   background-color: white;
   opacity: .10;
  
}
.innerFooter:hover{
    width: 100%;
    height: 400px; 
    background-color: rgb(1, 75, 58); 
    transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
    opacity: .55;
}
